
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#app .content {
  width: 800px;
  height: 500px;
  margin: 0px auto;
  border: 1px solid #eee;
}
.nb-scroll {
  position: relative;
  width: 100%;
  height: 100%;
}
.nb-scroll .item-list {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
}
.nb-scroll .item-list .item {
  position: absolute;
  width: 100%;
}
.nb-scroll .item-list .item > div {
  will-change: opacity;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 500ms;
          transition-duration: 500ms;
}
.nb-scroll .item-list .bottom {
  position: absolute;
  width: 100%;
  padding: 0px 0px;
  text-align: center;
  color: #aaa;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100px;
}
.nb-scroll .flash-list {
  width: 100%;
  height: 0px;
  overflow: hidden;
  position: relative;
  contain: layout;
  top: -100000px;
  position: absolute;
  visibility: hidden;
  z-index: -1000;
}
.nb-scroll .model {
  top: -1000px;
  visibility: hidden;
  position: absolute;
  width: 100%;
}
.nb-scroll .tombstone {
  border-bottom: 1px solid #eee;
  position: absolute;
  width: 100%;
}
.nb-scroll .runway {
  width: 1px;
  height: 1px;
  position: absolute;
}
